import {ajax,showTips,setTime,showLoading,hideLoading} from '@/ajax.js'
export default {
	data() {
		return {
			userInfo: null,
			oheight:'',
			list:[]
		}
	},
	onLoad() {
		let _this = this;
		uni.getSetting({ //判断是否授权
		  success (res){
			if (res.authSetting['scope.userInfo']) { //已经授权
			  uni.getUserInfo({
				lang:'zh_CN',
				success: function(res) {
					_this.userInfo = res.userInfo;
					uni.setStorageSync('userInfo', _this.userInfo);
				}
			  })
			}else{
				_this.userInfo = ''
			}
		  }
		})
		
		showLoading()
		uni.request({
			url: ajax.wbInfo, 
			header:{
				'Content-Type':'application/x-www-form-urlencoded'
			},
			method:'get',
			success: (rs) => {
				hideLoading()
				if(rs.data.code==200){
					rs.data.data.map(es=>{
						if(es.imgList!=null&&es.imgList!=''){
							es.imgList= JSON.parse(es.imgList)
						}
						es.time = setTime(es.startTime)
					})
					_this.list = rs.data.data;
					console.log(_this.list)
				}
			},
		})
	},
	methods: {
		goItem(item,index){
			uni.navigateTo({
				url: '/pages/details/details?id='+item+'&index='+index
			})
		},
		getUserInfo(e){ //授权成功
			this.userInfo=e.target.userInfo;
			uni.setStorageSync('userInfo', this.userInfo);
		},
		editBtn(){  //编写
			uni.navigateTo({
				url: '/pages/edit/edit'
			})
		}
	},
	
}
